












import { Component, Vue } from 'vue-property-decorator';
import MyDatePicker from '@/components/MyDatePicker.vue';
import ManPwrTracker from '@/components/ManPwrTracker.vue';
import ManPwrArray from '@/model/ManPwrArray';
import LostBattleArray from '@/model/LostBattleArray';

@Component({
  components: {
    'my-date-picker': MyDatePicker,
    'man-pwr-tracker': ManPwrTracker,
  },
})
export default class ManPwr extends Vue {
  private currentDate: string = (new Date()).toDateString();

  get manpwrArray(): ManPwrArray
  {
    return this.$store.state.manPwrArray;
  }

  get lostBattleArray(): LostBattleArray
  {
    return this.$store.state.lostBattleArray;
  }

  private get daysSinceLostBattle(): string
  {
    return (this.lostBattleArray.daysSinceLastLostBattle != -1) ? this.lostBattleArray.daysSinceLastLostBattle.toString() : "(No lost battle added)";
  }

  public get dateClass(): {[index: string]: string}
  {
    let output: {[index: string]: string} = {};
    this.manpwrArray.partialDates.forEach((elem) => {
      output[elem] = 'partialDate';
    });
    this.manpwrArray.fullDates.forEach((elem) => {
      output[elem] = 'fullDate';
    });
    this.lostBattleArray.list.forEach((elem) => {
      output[elem] = 'lostBattle';
    });
    return output;
  }

  get isSelectedDateLostBattle(): boolean
  {
    return this.lostBattleArray.isDateLostBattle(this.currentDate);
  }

  private addLostBattle()
  {
    this.lostBattleArray.addLostBattle(this.currentDate);
  }

  private removeLostBattle()
  {
    this.lostBattleArray.removeLostBattle(this.currentDate);
  }
}
