








import { Component, Prop, PropSync, Emit, Vue } from 'vue-property-decorator';

@Component
export default class ManPwrTracker extends Vue {
    @Prop() value!: string;

    @Emit('input')
    onInputChange(newValue: string) {}

    @Emit('valueChanged')
    onValueChange() {}

    get selectedDict(): { [key: string]: boolean }
    {
        let output: { [key: string]: boolean } = {};
        if(this.value != undefined)
        {
            this.keys.forEach((key) => { output[key] = (this.value.indexOf(key) != -1); })
        }
        else
        {
            this.keys.forEach((key) => { output[key] = false; })
        }
        return output;
    }

    @Emit('keyClicked')
    private keyClicked(key: string) 
    {
        let newValue = this.value;
        if(newValue == undefined)
        {
            newValue = key;
        }
        else if(newValue.indexOf(key) != -1)
        {
            newValue = newValue.replace(key,'');
        }
        else 
        {
            newValue += key;
        }
        this.onInputChange(newValue);
        this.onValueChange();
    }

    get keys(): string[] {
        return ["M", "A", "N", "P", "W", "R"];
    }
}
