



























import { Component, Prop, Emit, Vue } from 'vue-property-decorator';

@Component
export default class MyDatePicker extends Vue {
    private dayNames = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
    private monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
    private selectedDate: Date = new Date();
    private currentMonth: number = this.selectedDate.getMonth();
    private currentYear: number = this.selectedDate.getFullYear();
    @Prop({default: () => {return {};}}) dateClass!: {[index: string]: string};

    @Prop() value!: string;
    
    @Emit('input')
    onInputChange(newValue: string) {}

    created() { }

    private getDateClass(input: Date)
    {
        let output:{[index: string]: boolean} = {};
        output['currentDate'] = this.areDatesEqual(input, new Date());
        output['selectedDate'] = this.areDatesEqual(input, this.selectedDate);
        output['notRangeDate'] = !this.isDateWithinCurrentRange(input);
        if(Object.keys(this.dateClass).indexOf(input.toDateString()) != -1)
        {
            output[this.dateClass[input.toDateString()]] = true;
        }
        return output;
    }

    private dateClicked(date: Date)
    {
        if(this.selectedDate != date)
        {
            this.selectedDate = date;
            this.onInputChange(this.selectedDateString);
        }
    }

    private monthOffset(offset: number)
    {
        let tempDate = new Date(this.currentYear, this.currentMonth, 1);
        tempDate.setMonth(tempDate.getMonth() + offset);
        this.currentMonth = tempDate.getMonth();
        this.currentYear = tempDate.getFullYear();
    }

    get currentMonthString(): string { return this.monthNames[this.currentMonth]; }
    get currentYearString(): string { return this.currentYear.toString(); }
    get selectedDateString(): string { return this.selectedDate.toDateString(); }

    private isDateWithinCurrentRange(input: Date) {
        return (input.getMonth() == this.currentMonth) && (input.getFullYear() == this.currentYear);
    }

    private areDatesEqual(input1: Date, input2: Date) {
        return (input1.getDate() == input2.getDate())
            && (input1.getMonth() == input2.getMonth())
            && (input1.getFullYear() == input2.getFullYear());
    }

    private getDateString(input: Date): string {
        return input.getDate().toString();
    }

    private getDateFromDayOffset(input: Date, offset: number) {
        let output = new Date(input);
        output.setDate(output.getDate() + offset);
        return output;
    }

    get dateArray(): Date[][] {
        let output: Date[][] = [];
        let curWeek: number = 0;
        let curDate = new Date(this.currentYear, this.currentMonth, 1);

        // Pre-padding
        for(let i = 0; i < curDate.getDay(); i++)
        {
            if(!output[curWeek])
            {
                output[curWeek] = [];
            }
            output[curWeek].push(this.getDateFromDayOffset(curDate, -1 * (curDate.getDay() - i)));
        }

        // Filling out dates
        while((curDate.getMonth() == this.currentMonth)
            && (curDate.getFullYear() == this.currentYear))
        {
            if(curDate.getDay() == 0)              // The current day is Sunday
            {
                curWeek += 1;
            }

            if(!output[curWeek])
            {
                output[curWeek] = [];
            }
            output[curWeek].push(curDate);
            curDate = this.getDateFromDayOffset(curDate, 1);
        }

        // Post-padding
        while((curDate.getDay() != 0))
        {
            if(!output[curWeek])
            {
                output[curWeek] = [];
            }
            output[curWeek].push(curDate);
            curDate = this.getDateFromDayOffset(curDate, 1);
        }

        return output;
    }
}
